@use '../../styles/variables' as *;
@use '../../styles/typography' as *;
@use '../../styles/mixins' as *;
@use '../../styles/breakpoints' as *;

.formContainer{
    width: 80%;
    margin-inline: auto;
    
    pointer-events: none;

    &.active{
        pointer-events: auto;

        .input{
            background-color: white;
        }

        .btn{
            background-color: $blue-dark-1;
        }
    }
    .form{
        position: relative;
        width: 100%;
        max-width: 800px;
        overflow: hidden;
        border-radius: 0.1rem;
    }
}

.input{
    width: 100%;
    height: 100%;
    border: 2px solid transparent;
    padding-block: $pd-s;
    padding-left: $pd-nm;
    padding-right: 5rem;
    font-weight: 700;
    background-color: $gray-light-1;

    &::placeholder{
        color: rgba($color: $black, $alpha: 0.3);
        font-weight: 700;
    }

}

.btn{
    cursor: pointer;
    position: absolute;
    height: 100%;
    right: 0;
    border: 0;
    border-radius: 0 0.1rem 0.1rem 0;
    outline: none;
    width: 4rem;
    background-color: $gray-light-1;
    
    @include transition;

    &:hover{
        background-color: rgba($color: $blue-dark-1, $alpha: 0.85);
    }

    .icon{ 
        color: $white;
        font-size: 1.2rem;
    }
}